<template>
    <v-layout class="previewer-box">
        <div class="previewer" ref="previewer"></div>
    </v-layout>
</template>

<script>
    import SkinSteve from '../../assets/skin-steve.png'
    import * as skin3d from 'skinview3d'
    export default {
        name: 'Previewer',
        data: () => ({
            previewer: null,
            walk: null,
            run: null,
            rotate: null,
            control: null,
            paused: false,
        }),
        beforeDestroy () {
            if (this.previewer)
                this.previewer.dispose();
        },
        methods: {
            initPreviewer () {
                this.previewer = new skin3d.SkinViewer({
                    domElement: this.$refs.previewer,
                    width: this.$refs.previewer.clientWidth,
                    height: this.$refs.previewer.clientHeight,
                    detectModel: true,
                    skinUrl: SkinSteve,
                    capeUrl: null
                });
                this.previewer.animation = new skin3d.CompositeAnimation();
                this.walk = this.previewer.animation.add(skin3d.WalkingAnimation);
                this.run = this.previewer.animation.add(skin3d.RunningAnimation);
                this.rotate = this.previewer.animation.add(skin3d.RotatingAnimation);
                this.control = skin3d.createOrbitControls(this.previewer);
                this.run.paused = true;
                this.rotate.paused = true;
            },
            togglePaused () {
                this.paused = !this.paused;
                this.previewer.animationPaused = !this.previewer.animationPaused;
            },
            toggleRun () {
                this.run.paused = !this.run.paused;
                this.walk.paused = false;
            },
            toggleRotate () {
                this.rotate.paused = !this.rotate.paused;
            },
            setSkin (skin) {
                this.previewer.skinUrl = skin || SkinSteve;
            },
            setCape (cape) {
                this.previewer.capeUrl = cape;
            },
            isPaused () {
                return this.paused;
            }
        }
    }
</script>

<style lang="stylus">
    .previewer-box
        @media (min-width 959px)
            height 500px
        > .previewer
            min-width 85%
            cursor move
            canvas
                filter drop-shadow(-5px 5px 7px rgba(0, 0, 0, 0.4))
            @media (max-width 959px)
                min-width 100%
</style>
