<template lang="pug">
    v-flex
        v-toolbar(dense flat color="white" style="z-index: 2;").elevation-1
            v-toolbar-title
                span.subheading {{ title }}
                span(v-if="subtitle" style="font-size: 0.9rem; color: grey;").px-2 {{ subtitle }}
            v-spacer
            v-btn(icon @click="toggleRun")
                v-icon mdi-fast-forward
            v-btn(icon @click="toggleRotate")
                v-icon mdi-refresh
            v-btn(icon @click="togglePaused")
                // TODO 
                i(class="mdi-pause").v-icon.mdi
        v-card(style="z-index: 1;")
            Previewer(ref="previewer")
</template>

<script>
    import Previewer from '@/components/common/Previewer.vue'
    export default {
        name: 'UserPreviewer',
        components: {
            Previewer
        },
        props: {
            title: {
                type: String,
                required: true
            },
            subtitle: {
                type: String,
                default: null
            }
        },
        methods: {
            init () { this.$refs.previewer.initPreviewer() },
            toggleRun () { this.$refs.previewer.toggleRun() },
            toggleRotate () { this.$refs.previewer.toggleRotate() },
            togglePaused () { this.$refs.previewer.togglePaused() },
            setSkin(skin) { this.$refs.previewer.setSkin(skin) },
            setCape (cape) { this.$refs.previewer.setCape(cape) },
            isPaused () {
                if (!this.$refs.previewer)
                    return false;
                return this.$refs.previewer.isPaused();
            }
        }
    }
</script>

